.overlay{
    background-image: url(https://images.unsplash.com/photo-1553877522-43269d4ea984?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxzZWFyY2h8Mnx8YWdlbmN5fGVufDB8fDB8fA%3D%3D&auto=format&fit=crop&w=500&q=60);
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    height: 80vh;
    z-index: 2;
}

.secondoverlay{
    background-color: black;
    opacity: 70%;
    width: 100vw;
    height: 80vh;
}

.hero-heading{
    position: relative;
    top: 25%;
    left: 4%;
    /* width: 50%; */
}

.hero-header{
    /* font-size: 40px; */
    font-weight: bold;
    margin-bottom: 20px;
    width: 90%;
}

.hero-subheader{
    margin-bottom: 20px;
    width: 90%;
}

.changing-text{
    color: teal;
}

.button-color{
    background-color: teal;
    width: 105px;
    height: 40px;
    transition: width 2s;
}

.button-color:hover{
    width: 180px;
}

.button-text{
    top: -33px;
    left: 50px;
    font-weight: bold;

}